<template>
  <div class="mt-10 mx-2 md:w-1/2 md:mx-auto poppins pb-10">
    <vee-form
      :validation-schema="schema"
      @submit="register"
      :initial-values="userData"
    >
      <!-- Name -->
      <div class="mb-3">
        <label class="inline-block mb-2 dark:text-white">Name</label>
        <vee-field
          type="text"
          name="name"
          class="
            block
            w-full
            py-1.5
            px-3
            text-gray-800
            border border-gray-300
            transition
            duration-500
            focus:outline-none
            focus:border-black
            rounded
          "
          placeholder="Enter Name"
        />
        <ErrorMessage class="dark:text-red-500 text-red-600" name="name" />
      </div>
      <!-- Email -->
      <div class="mb-3">
        <label class="inline-block mb-2 dark:text-white">Email</label>
        <vee-field
          type="email"
          name="email"
          class="
            block
            w-full
            py-1.5
            px-3
            text-gray-800
            border border-gray-300
            transition
            duration-500
            focus:outline-none
            focus:border-black
            rounded
          "
          placeholder="Enter Email"
        />
        <ErrorMessage class="dark:text-red-500 text-red-600" name="email" />
      </div>

      <!-- Phone Number -->
      <div class="mb-3">
        <label class="inline-block mb-2 dark:text-white">Phone Number</label>
        <vee-field
          type="tel"
          name="phone"
          class="
            block
            w-full
            py-1.5
            px-3
            text-gray-800
            border border-gray-300
            transition
            duration-500
            focus:outline-none
            focus:border-black
            rounded
          "
          placeholder="e.g. 01612345678"
        />
        <ErrorMessage class="dark:text-red-500 text-red-600" name="phone" />
      </div>
      <!-- College -->
      <div class="mb-3">
        <label class="inline-block mb-2 dark:text-white">College</label>
        <vee-field
          type="college"
          name="college"
          class="
            block
            w-full
            py-1.5
            px-3
            text-gray-800
            border border-gray-300
            transition
            duration-500
            focus:outline-none
            focus:border-black
            rounded
          "
          placeholder="College Name"
        />
        <ErrorMessage class="dark:text-red-500 text-red-600" name="college" />
      </div>

      <!-- Email -->
      <div class="mb-3">
        <label class="inline-block mb-2 dark:text-white">SSC Board</label>
        <vee-field
          as="select"
          name="ssc_board"
          class="
            block
            w-full
            py-1.5
            px-3
            text-gray-800
            border border-gray-300
            transition
            duration-500
            focus:outline-none
            focus:border-black
            rounded
          "
          placeholder="Board"
        >
          <option value="barisal">Barisal</option>
          <option value="chittagong">Chittagong</option>
          <option value="comilla">Comilla</option>
          <option value="dhaka">Dhaka</option>
          <option value="dinajpur">Dinajpur</option>
          <option value="jessore">Jessore</option>
          <option value="madrasah">Madrasah</option>
          <option value="rajshahi">Rajshahi</option>
          <option value="sylhet">Sylhet</option>
          <option value="mymensingh">Mymensingh</option>
          <option value="tec">Technical</option>
        </vee-field>
        <ErrorMessage class="dark:text-red-500 text-red-600" name="ssc_board" />
      </div>
      <div class="mb-3">
        <label class="inline-block mb-2 dark:text-white">SSC Roll</label>
        <vee-field
          type="tel"
          name="ssc_roll"
          class="
            block
            w-full
            py-1.5
            px-3
            text-gray-800
            border border-gray-300
            transition
            duration-500
            focus:outline-none
            focus:border-black
            rounded
          "
          placeholder="SSC Roll"
        />
        <ErrorMessage class="dark:text-red-500 text-red-600" name="ssc_roll" />
      </div>
      <div class="mb-3">
        <label class="inline-block mb-2 dark:text-white"
          >SSC Registration</label
        >
        <vee-field
          type="tel"
          name="ssc_reg"
          class="
            block
            w-full
            py-1.5
            px-3
            text-gray-800
            border border-gray-300
            transition
            duration-500
            focus:outline-none
            focus:border-black
            rounded
          "
          placeholder="SSC Registration NO"
        />
        <ErrorMessage class="dark:text-red-500 text-red-600" name="ssc_reg" />
      </div>
      <div class="mb-3">
        <label class="inline-block mb-2 dark:text-white"
          >Facebook ID Link</label
        >
        <vee-field
          type="text"
          name="facebook_link"
          class="
            block
            w-full
            py-1.5
            px-3
            text-gray-800
            border border-gray-300
            transition
            duration-500
            focus:outline-none
            focus:border-black
            rounded
          "
          placeholder="Facebook ID Link"
        />
        <ErrorMessage class="dark:text-red-500 text-red-600" name="ssc_reg" />
      </div>
      <div
        :class="reg_alert_variant"
        class="text-white text-center font-bold p-5 my-4"
        v-if="reg_show_alert"
      >
        {{ reg_alert_msg }}
      </div>
      <button
        type="submit"
        class="
          block
          w-full
          bg-red-500
          text-white
          py-1.5
          px-3
          rounded
          transition
          hover:bg-red-600
        "
        :disabled="reg_in_submission"
      >
        Register
      </button>
    </vee-form>
  </div>
</template>

<script>
export default {
  name: "RegisterFrom",
  beforeRouteEnter(to, from, next) {
    let user = localStorage.getItem("ssrb");
    if (user == undefined) {
      next();
      return;
    }
    next({
      path: "/",
    });
  },
  data() {
    return {
      schema: {
        name: "required|min:3|max:100|alpha_spaces",
        email: "required|min:3|max:100|email",
        phone: "required|min:10|max:13|",
        college: "required|min:4|max:100|alpha_spaces",
        ssc_board: "required",
        ssc_roll: "required:min:4",
        ssc_reg: "required|min:5",
        facebook_link: "required",
      },
      userData: {
        phone: this.$route.query.phone,
      },

      reg_in_submission: false,
      reg_show_alert: false,
      reg_alert_variant: "bg-blue-500",
      reg_alert_msg:
        "Please wait! Untill your registration has been completed.",
    };
  },
  methods: {
    async register(values) {
      this.reg_show_alert = true;
      this.reg_in_submission = true;
      this.reg_alert_variant = "bg-blue-500";
      this.reg_alert_msg =
        "Please wait! Untill your registration has been completed";
      let pl = values.phone.length;
      let phone = values.phone.substring(pl - 10, pl);
      try {
        this.$store.dispatch("register", {
          body: JSON.stringify({
            ...values,
            phone,
          }),
          router: this.$router,
          phone,
        });
      } catch (err) {
        console.log(err);
        this.reg_in_submission = false;
        this.reg_alert_variant = "bg-red-500";
        this.reg_alert_msg =
          "An Unexpected error occurred. Please try again later.";
        return;
      }

      this.reg_alert_variant = "bg-green-500";
      this.reg_alert_msg = "Success! Your account has been registered.";
    },
  },
};
</script>

<style lang="scss" scoped>
</style>
